import { createStore } from 'vuex'

export default createStore({
  state: {
    isHome: null,
    path: "/"
  },
  getters: {
    getPath(state){
      return state.path;
    },
    getIsHome(state){
      return state.isHome;
    }
  },
  mutations: {
    changeStorePath(state, path) {
      state.path = path;
    }
  },
  actions: {
  },
  modules: {
  }
})
