<template>
  <div class="contact">
    <h2 class="contact-title">Formulaire de contact (en travaux)</h2>
    <form id="mail-form" class="contact-form">
      <label class="contact-form_label">
        Adresse mail:
        <input :class="{'red-input': badMailAddress || emptyMail}" class="contact-form_input" type="email" name="email" id="email" required disabled>
        <div v-if="badMailAddress || emptyMail">
          <span v-if="badMailAddress">L'adresse email n'est pas valide</span>
          <span v-if=" emptyMail">Ce champ est obligatoire</span>
        </div>
      </label>
      <label class="contact-form_label">
        Object:
        <input :class="{'red-input': emptyObject}" class="contact-form_input" type="text" name="object" id="object" required disabled>
        <div v-if="emptyObject">Ce champ est obligatoire</div>
      </label>
      <label class="contact-form_label">
        Message:
        <textarea :class="{'red-input': emptyBody}" class="contact-form_input" name="mail-body" id="mail-body" cols="30" rows="10" required  disabled></textarea>
        <div v-if="emptyBody">Ce champ est obligatoire</div>
      </label>
      <button type="submit" id="mail-submit"  disabled>Envoyer</button>
    </form>
  </div>
</template>
    
<script>
// @ is an alias to /src

export default {
    name: 'HomeView',
    data() {
      return {
        badMailAddress: false,
        emptyMail: false,
        emptyObject: false,
        emptyBody: false
      }
    
    },
    mounted() {
      document.querySelector('#mail-submit').addEventListener('click', e => {
        e.preventDefault();
        e.stopPropagation();

        let form = document.querySelector('#mail-form').elements;
        let mailSender = form['email'].value;
        let mailObject = form['object'].value;
        let mailBody = form['mail-body'].value;

        function ValidateEmail(mail) {
          // eslint-disable-next-line
          if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
            return true;
          } else {
            return false;
          }
        }

        if(!form['email'].value) {
          this.emptyMail = true;
          this.badMailAddress = false;
        } else if(!ValidateEmail(mailSender)) {
          form['email'].classList.add('red-input');
          this.emptyMail = false;
          this.badMailAddress = true;
          return false;
        } else {
          form['email'].classList.remove('red-input');
          this.emptyMail = false;
          this.badMailAddress = false;
        }

        if(!form['object'].value) {
          this.emptyObject = true;
        } else {
          this.emptyObject = false;
        }

        if(!form['mail-body'].value) {
          this.emptyBody = true;
        } else {
          this.emptyBody = false;
        }

        mailSender = String(mailSender);
        mailObject = String(mailObject);
        mailBody = String(mailBody);

        let mailData = {
          mailSender,
          mailObject,
          mailBody
        }

        console.log(mailData);
      })
    }
}
</script>

<style lang="scss">
  .contact {
    width: 80%;
    height: 100%;
    max-width: 800px;
    margin: auto;
    padding: 1em;
    background: rgba(0,0,0, 0.6);
    border-radius: 0.5em;

    @media (max-width: 600px) {
        width: 100%;
        height: 100%;
        transform: translateY(-2em);
    }

    &-title {
      margin-bottom: 0.5em;

      @media (max-width: 600px) {
        display: none;
      }
    }

    &-form {
      display: flex;
      flex-direction: column;

      &_label {
        margin-top: 1em ;
        display: flex;
        flex-direction: column;
        align-items: start;
        min-height: 80px;
      }

      &_input {
        width: 100%;
        min-height: 30px;
        margin-top: 0.5em;
        border-radius: 0.4em
      }

      button {
        margin-top: 1em;
        width: 80px;
        padding: 1em;
        align-self: center;
      }
    }
  }


  #mail-body {
    resize: vertical;
  }

  .red-input {
    border: 3px solid red;
  }
</style>
