<template>
    <div @mouseleave="hideEverything"
        class="project_card"
     >
        <div class="project_card-image">
            <img :src=getProjectCover() :alt=coverAlt >
        </div>
        <div             :class="{'hideCd': !showCd, 'showCd': showCd }"
            class="project_card-infos">

            <div @click="showEverything" class="project_card-infos_void">
                <span class="project_card-infos_void-style">
                    {{style}}
                </span>
            </div>
            <div @click="showLinks = true" :class="{'cdBorder': showCd }" class="project_card-infos_container" >
                <div class="project_card-infos_container--block project_card-infos_container--project">
                    <div :class="{'fadeOut': !showProjectMainInfos, 'fadeIn': showProjectMainInfos }"
                     class="project_card-infos_container--project_band bold">
                        {{ band }}
                    </div>
                    <div class="project_card-infos_container--project_album">
                        <span v-if="!showCd">{{ album }}</span>
                        <span v-if="showCd">Ecouter ce projet</span>
                    </div>
                </div>
                <div class="project_card-infos_container--block project_card-infos_container--description">
                </div>
                <div
                    class="project_card-infos_container--block project_card-infos_container--work"
                    :class="{'hideWork': !showWork, 'showWork': showWork }"
                >
                    <span v-for="work of works"
                        :key = "work"
                        class="project_card-infos_container--work_item"
                        >
                        {{ work }}
                    </span>
                </div>
            </div>
        </div>
    
      <LinksPopIn class="links" v-show="showLinks"
      :linksList = links
      />        
    </div>


</template>

<script>
import LinksPopIn from '@/components/LinksPopIn.vue';

export default {
  name: 'ProjectCard',
  components: {
    LinksPopIn
  },
  props: [
    'cover',
    'coverAlt',
    'band',
    'album',
    'length',
    'style',
    'works',
    'links'
  ],
  data() {
    return {
        showCd: false,
        showWork: false,
        showProjectMainInfos: true,
        showLinks: false
    }
  },
  mounted() {
    document.querySelectorAll('.project_card-infos_container--work_item')
        .forEach(item => {
            if(item.innerHTML == "Rec") {
                item.classList.add('work')
            } else if(item.innerHTML == "Mix") {
                item.classList.add('work')
            } else if(item.innerHTML == "Master") {
                item.classList.add('work')
            }
        })
  },
  methods: {
    getProjectCover() {
        return require("@/assets/images/projects/" + this.cover)
    },
    showEverything() {
        this.showCd = true;
        this.showWork = true;
        this.showProjectMainInfos = false;
    },
    hideEverything() {
        this.showCd = false;
        this.showWork = false;
        this.showProjectMainInfos = true;
        this.showLinks = false;
    },
    toggleLinksPopIn() {
      if(this.showLinksPopIn == false) {
        this.showLinksPopIn = true;
        this.fadeLinksPopIn = true;
      } else {
        this.fadeLinksPopIn = false;
        setTimeout(() => {
          this.showLinksPopIn = false;
        }, 500);
      }
    }
  }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.project_card {
    cursor: pointer;
    width: 250px;
    height: 250px;
    box-shadow: 5px 5px 20px black;
    border-radius: 0.5em;
    display: flex;
    position: relative;
    overflow: hidden;

    transform: scale(0.99);

    &:hover {
        transform: scale(1);
    }
    
    &-image {
        & img {
            width: 100%;
            height: 100%;
            overflow: hidden;
            object-fit: cover;
        }
    }

    &-infos {
        color: white;
        width: 100%;
        position: absolute;
        bottom: 0;
        transition: 800ms;

        &_void {
            display: block;
            height: 250px;
            background: transparent;
            z-index: 10;
            position: relative;

            &-style {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 25px;
                width: 100%;
                background: rgba(0,0,0,0.7);
                font-size: 0.9em;
                transform: translateY(-0.1px);
            }
        }

        &_container {
            height: 250px;
            width: 100%;
            position: relative;

            display: flex;
            flex-direction: column; 
            justify-content: start;

            background: rgb(255,255,255);
            background: radial-gradient(circle,
                rgba(255,255,255,0) 0%, rgba(255,255,255,0) 5%, rgba(255,255,255,0.2) 6%, rgba(255,255,255,0.2) 8%,
                rgba(192,192,192,0.2) 9%, rgba(192,192,192,0.2) 12%, rgba(161,161,161,0.5) 14%, rgba(161,161,161,0.5) 15%,
                rgba(0,0,0,0.8) 16%, rgba(0,0,0,0.8) 100%);     
            border-radius: 100%;
            transform: scale(0.96) translateY(-75px);


            &--block {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                z-index: 50;
            }

            &--project {
                transition: 800ms;
                height: 60px;
                display: flex;
                flex-direction: column;
                justify-content: end;
                align-items: center;

                &_band, &_album {
                    height: 30%;
                }

                &_band {
                    font-size: 1.1em;
                }

                &_album {
                    font-size: 0.9em;
                }
            }

            &--description {
                display: flex;
                justify-content: start;
                height: 110px;
                opacity: 0.8;
                transition: 800ms;

                &_length {
                    font-weight: italic;
                    font-size: 1em;
                }

                &_links {
                height: 20px;
                font-size: 0.8em;
                font-style: italic;
                }
            }

            &--work {
                height: 70px;
                display: flex;
                transition: 800ms;
                font-size: 0.9em;

                &_item {
                    width: 80px;
                    height: 18px;
                    border-radius: 0.2em;   
                    margin-bottom: 5px;
                }
            }
        }


    }
    
}

.links {
  position: absolute;
  top: 0;
  border-radius: 0.5em;
  z-index: 500;
}

.work {
    background: rgba(#525252, 0.20);
    box-shadow: 1px 1px 1px rgba(#525252, 0.33);
}

// .mix {
//     background: rgba(#525252, 0.66);
//     box-shadow: 1px 1px 1px rgba(#525252, 0.66);
// }

// .master {
//     background: rgba(#525252, 1);
//     box-shadow: 1px 1px 1px rgba(#525252, 1);
// }

.hideCd {
    transform: translateY(250px);
}

.showCd {
    transform: translateY(72px);
}

.cdBorder {
    border: 2px solid rgba(#a1a1a1, 0.6);
    z-index: 20;
}

.hideWork {
    transform: translate(-200px);
}

.showWork {
    transform: translate(0);
}

.fadeOut {
    opacity: 0;
}

.fadeIn {
    opacity: 1;
}
</style>
