<template>
  <div class="home">
    <div class="home-description">
      <div class="home-description-text">
        Shifting Process Records est un studio basé à Déville-lès-Rouen en Seine Maritime
        <br />
        <br />
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Fusce pharetra lectus tincidunt mi porttitor,
        iaculis eleifend dolor venenatis. Curabitur pellentesque eu turpis quis fermentum. In pharetra facilisis
        quam vel rhoncus. Donec ultrices tincidunt risus, vel sagittis ipsum commodo condimentum. Aliquam eget

      </div>
      <div class="home-description_socials">
        <a class="home-description_socials-link" href="https://facebook.com/shiftingprocess" target="_blank">
          <button>
            Suivez nous sur Facebook ! <font-awesome-icon class="home-description_socials-icon" icon="fa-brands fa-facebook" />
          </button>
        </a>
        <!-- <a href="" target="_blank">
          <font-awesome-icon class="home-description_socials-icon" icon="fa-brands fa-instagram" />
        </a>  -->
      </div>

    </div> 
    <div class="home-carrousel">
      <span v-if="!carrouselStart" @click="moveCarrousel('left');" class="home-carrousel_left">
        <font-awesome-icon icon="fa-solid fa-chevron-left" />
      </span>
      <div class="home-carrousel_img-box">
        <img src="@/assets/images/homeImages/test3.jpg" alt="" />
        <img src="@/assets/images/homeImages/test5.jpg" alt="" />
        <img src="@/assets/images/homeImages/test6.jpg" alt="" />
        <img src="@/assets/images/homeImages/test7.jpg" alt="" />
        <img src="@/assets/images/homeImages/test8.jpg" alt="" />
        <img src="@/assets/images/homeImages/test9.jpg" alt="" />
        <img src="@/assets/images/homeImages/test10.jpg" alt="" />
        <img src="@/assets/images/homeImages/test12.jpg" alt="" />
      </div>
      <span v-if="!carrouselEnd" @click="moveCarrousel('right');" class="home-carrousel_right">
        <font-awesome-icon icon="fa-solid fa-chevron-right" />
      </span>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src


export default {
  name: 'HomeView',
  data() {
    return {
      carrouselStart: true,
      carrouselEnd: false,
      imgQuantity: 0,
      imgShown: 1
    }
  },
  methods: {
    moveCarrousel(direction) {
      let carrouselWidth = document.querySelector('.home-carrousel').clientWidth;
      // let imgBoxWidth = document.querySelector('.home-carrousel_img-box').clientWidth;
      let translateProperty = document.querySelector('.home-carrousel_img-box').style.transform;
      let actualTranslateValue = - Number(translateProperty.match('[0-9]+'));
      if (!actualTranslateValue) actualTranslateValue = 0;

      if(direction == "left" && this.carrouselStart === false) {
        this.imgShown--;
        document.querySelector('.home-carrousel_img-box').style.transform = `translateX(${actualTranslateValue + carrouselWidth}px)`;
        console.log(this.imgShown, this.imgQuantity)
        if (this.imgShown === 1) {
          this.carrouselStart = true;
          this.carrouselEnd = false;
        } else {
          this.carrouselStart = false;
          this.carrouselEnd = false;
        }
      } else if (direction == "right" && this.carrouselEnd === false) {
        this.imgShown++;
        document.querySelector('.home-carrousel_img-box').style.transform = `translateX(${actualTranslateValue - carrouselWidth}px)`;
        console.log(this.imgShown, this.imgQuantity)
        if (this.imgShown === this.imgQuantity) {
          this.carrouselStart = false;
          this.carrouselEnd = true;    
        } else {
          this.carrouselStart = false;
          this.carrouselEnd = false;
        }
      }
    }
  },
  mounted() {
    this.imgQuantity = document.querySelector('.home-carrousel_img-box').children.length;

    let carrousel = document.querySelector('.home-carrousel');
    window.addEventListener('resize', () => {
      let newCarrouselWidth = carrousel.clientWidth;
      let previousTranslateProperty = document.querySelector('.home-carrousel_img-box').style.transform;
      let previousTranslateValue = Number(previousTranslateProperty.match('[0-9]+'));
      let difference = previousTranslateValue - newCarrouselWidth;

      if(previousTranslateValue) {
        console.log(previousTranslateValue, newCarrouselWidth, difference)
      document.querySelector('.home-carrousel_img-box').style.transform = `translateX(${- previousTranslateValue + difference}px)`;
      }
    })

    window.addEventListener('keyup', e => {
      if(e.key === "ArrowRight") {
        this.moveCarrousel('right');
      } else if (e.key === "ArrowLeft") {
        this.moveCarrousel('left');
      }
    })
  }
}
</script>

<style lang="scss">
.home {
  width: 80%;
  // height: 100%;
  border-radius: 0.5em;
  margin: 0 auto;
  background: rgba(0,0,0, 0.6);
  display: flex;
  align-items: center;

  @media (min-width: 1200px) {
    flex-direction: row;
    justify-content: space-between;

    &-description {
      flex-basis: 40%;
      height: 100%;
      flex: 1;
    }

    &-carrousel {
      flex-basis: 55%;
      height: 100%;
      transform: scale(1.02);

      &_img-box img {
        object-position: left;
      }
    }
  };

  @media (max-width: 1199px) {
    flex-direction: column;

    &-description {
      // flex-basis: 40%;
    }

    &-carrousel {
      // flex-basis: 60%;
      width: 100%;

      &_img-box img {
        object-position: top left;
      }
    }
  };


  &-description {
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;

    &-text {
      padding-top: 2em;
      flex: 1;
      display: flex;
    }

    &_socials {
      align-self: baseline;
      width: 100%;
      height: 50px;

      &-link {
        // display: inline-block;
        
        button {
          height: 40px;
          background: transparent;
          border: none;
          border-radius: 1.2em;
          padding: 0.5em;
          color: white;
          cursor: pointer;
          font-size: 1em;

          //   &:hover {

          //   // .home-description_socials-icon {
          //   //   transform: scale(1.5);
          //   // }
          // }
        }


      }
      
      &-icon {
        margin-left: 0.5em;
        font-size: 1.4em;
      }
    }
  }

  &-carrousel {

    position: relative;
    border-radius: 0.5em;
    object-fit: cover;
    overflow: hidden;
    background: rgba(0,0,0, 0.6);

    &_left, &_right {
      height: 100%;
      width: 40px;
      position: absolute;
      background: rgba(0,0,0, 0.6);
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      top: 0;
      z-index: 50;
    }

    &_left {
      left: 0;
    }

    &_right {
      right: 0;
    }

    &_img-box {
      display: flex;
      flex-direction: row;
      height: 100%;
      transition: 400ms;

      img {
        height: 100%;
        min-width: 100%;
        object-fit: cover;
        object-position: top;
      }
    }
  }
}
</style>
