import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { 
  faGlobe, faChevronLeft, faChevronRight
 } from '@fortawesome/free-solid-svg-icons'
import { 
    faFacebook, faInstagram, faSpotify, faBandcamp, faYoutube, faLinkedin
  } from '@fortawesome/free-brands-svg-icons';

/* add icons to the library */
library.add(
  faFacebook, faInstagram, faBandcamp, faGlobe, faSpotify, faYoutube, faLinkedin, faChevronLeft, faChevronRight
  );

createApp(App)
    .use(store)
    .use(router)
    .component('font-awesome-icon', FontAwesomeIcon)
    .mount('#app')
